import i18n from 'i18next';
import LanguageDetector from 'i18next-browser-languagedetector';


const options = {
    fallbackLng: 'ru',
    // have a common namespace used around the full app
    ns: ['translations'],
    defaultNS: 'translations',
    keySeparator: false, // we use content as keys
    interpolation: {
        formatSeparator: ','
    },
    react: {
        wait: true
    }
};

options.resources = {
    en: {
        translations: {
            "btnAuthorization": "Log in",
            "city": "City",
            "admissionDate": "Admission to courses",
            "trainingDate": "Training dates",
            "numbers": "Numbers",
            "e-mail": "E-mail",
            "password": "Password",
            "courseName": "ourse name",
            "start": "Start",
            "end": "End",
            "courseType": "Course type",
            "add": "Add",
            "time": "Time",
            "lecturer": "Lecturer",
            "place": "Place",
            "duration": "Duration",
            "logIn": "Log in",
            "rememberMe": "Remember me",
            "forgotPassword": "Forgot password ?",
            "comments": "Comments"
        }
    },
    ru: {
        translations: {
            "btnAuthorization": "",
            "city": "",
            "admissionDate": "  ",
            "trainingDate": " ",
            "numbers": ":",
            "e-mail": "  ",
            "password": "",
            "courseName": " ",
            "start": "",
            "end": "",
            "courseType": " ",
            "add": "",
            "time": "",
            "lecturer": "",
            "place": "",
            "duration": "",
            "logIn": "",
            "rememberMe": " ",
            "forgotPassword": "  ?",
            "comments": ""
        }
    }
};

export default () => {
    i18n
        .use(LanguageDetector)
        .init(options);
    return i18n;
};


